.form{
  padding: 0.4rem;
  height: 6rem;
  overflow: auto;
}

.control{
  display: flex;
  justify-content: space-around;
  align-items: center;
  gap: 0.6rem;
}

.control label{
  width:16%;
  font-size: 19px;
  padding-right: 1.8rem;
}

.control input{
  padding:0.3rem;
  border-radius :6px; 
  font-size: 16px;
  flex: 1;
}

.control input {
  margin-bottom: 0.3rem;

}

.control input:focus,
.control input:active{
  outline: none;
  border: 2px solid #030f3f ;
  background-color: #3f56b1;
}

.invalid input:focus,
.invalid input :active {
  border: 2px solid rgb(76, 5, 5) ;
  background-color: rgb(132, 38, 38)
}

.actions{
  text-align: center;
}

.actions button :hover{
  cursor: pointer;
}

.actions :nth-child(1){
  margin-right: 0.6rem;
  background-color: #5a1a01;
  color: white;
}


.actions :nth-child(1):disabled{
  background-color: #4a4a4a;
  cursor: not-allowed;
}


